@import url('https://fonts.googleapis.com/css2?family=Sora&display=swap');

body{
    font-family: "Sora", sans-serif;
}

.item-button button{
    width: 200px;
     margin-top: 20px;
     background-color: rgb(138, 99, 15);
     color: white;
}
.spinner{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
}
.spinner-border{
height: 100px;
width: 100px;
}
.category{
    border: 2px solid rgb(128, 128, 128);
    margin-right: 25px;
    margin-bottom: 10px;
    padding: 10px;
    cursor: pointer;
}
.category img{
    margin-left: 10px;
}
.category-active{
    border: 2px solid green;
    background-color: rgba(128, 128, 128, 0.518);
}
/* registerpage */
.register{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
    
}
.register input{
    width: 300px; 
}
.register-form{
    background-color: rgb(255, 230, 0);
    padding: 20px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset; 
}
.register-form h1{
    text-align: center;
    background-color: rgb(199, 93, 7);
    color: white;
    padding: 10px;
}
.register-form h3{
    color: rgb(3, 3, 248);
    text-align: center;
}